$backgroundColor: #ffffff;
$bodyColor: #000000;

* {
  font-family: 'Playfair Display', serif;
}

body {
  background: $backgroundColor;
  color: $bodyColor;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#navbar-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: black;
}

#navbar {
  width: 90%;
  max-width: 1000px;
}

.nav-links {
  display: flex;
  justify-content: flex-end;
  list-style: none;
  flex-direction: row;
}

.nav-links > li {
  display: flex;
  align-items: center;
  padding: 1em 50px;
  height: 30px;
}

.nav-links :hover {
  background-color: #aaa;
}

#content-container {
  width: 90%;
  max-width: 1000px;
  padding: 25px;
}

h1 {
  font-size: 3em;
  padding: 0.5em 0;
  margin-bottom: 1em;
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: black;
}

h2 {
  font-size: 2em;
}

p {
  font-size: 1.5em;
  margin-bottom: 0.5em;
}

a:hover {
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.3);
}

#latest-post {
  border-style: solid;
  border-width: 0 0 1px 0;
  border-color: black;
  margin: 2em 0 2em 0;
}